.buzzer {
  border-radius: 100%;
  width: 70vw;
  height: 70vw;
  max-width: 400px;
  max-height: 400px;
  border: 5px solid lightgrey;
}
.pressable {
  background-color:rgb(221, 48, 48);
}
.nonPressable {
  background-color: rgb(131, 24, 24);
}
.pressable:hover {
  background-color: rgb(196, 51, 51);
}
.pressable:active {
	position:relative;
	top:1px;
  background-color: rgb(167, 26, 26);
}